@font-face {
  font-family: "Noir Pro";
  src: url("../assets/fonts/NoirPro-Light.ttf") format("ttf");
  font-weight: 200;
}
@font-face {
  font-family: "Noir Pro";
  src: url("../assets/fonts/NoirPro-Regular.ttf") format("ttf");
  font-weight: 400;
}
@font-face {
  font-family: "Noir Pro";
  src: url("../assets/fonts/NoirPro-Medium.ttf") format("ttf");
  font-weight: 600;
}
@font-face {
  font-family: "Noir Pro";
  src: url("../assets/fonts/NoirPro-Bold.ttf") format("ttf");
  font-weight: 800;
}
@font-face {
  font-family: "Noir Pro";
  src: url("../assets/fonts/NoirPro-Heavy.ttf") format("ttf");
  font-weight: 900;
}
* {
  font-family: "Noir Pro", sans-serif;
}
.heading{
    font-weight: bold !important;
    line-height: 1 !important;
    &--1{
        font-size: 3.75rem !important;
    }
    &--2{
        font-size: 3rem !important;
    }
}
.paragraph{
    font-size: 1.875rem !important;
    &--2{
      font-size: 1.25rem !important;
    }
}
.text{
  &--white{
    color: #fff;
  }
  &--black{
    color: #000;
  }
}
