@import '../../stylesheets/colors';
.main-banner {
  min-height: 700px;
//   height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(
    130deg,
    rgba(255, 255, 255, 1) 47%,
    rgba(207, 233, 245, 1) 47%
  );
  div.ant-typography, .ant-typography p{
      font-size: 3rem;
  }
  @media screen and (max-width: 996px){
    div.ant-typography, .ant-typography p{
        font-size: 2.25rem;
    }
  }
  &__cta {
    position: relative;
    @media screen and (max-width: 996px) {
        // display: flex;
    }
  }
  &__gift {
      margin-bottom: 30px;
    @media screen and (min-width: 996px) {
      position: absolute;
      top: -25%;
      right: 0;
      margin-bottom: 0;
    }

    background: #038fde;
    border-radius: 8px;
    margin-left: auto;

    max-width: 350px;
    text-align: center;
    box-shadow: -10px 10px 15px rgba(0, 0, 0, 0.2);
    &-content {
      padding: 30px 50px 20px;
      // width: 100%;
      // height: 100%;
      // background: #000;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: relative;
      overflow: hidden;
      border-radius: inherit;
      &:before,
      &:after {
        content: "";
        display: block;
        position: absolute;
        z-index: 0;
        width: 100%;
        height: 100%;
        background: #2ca1e3;
      }
      &:before {
        top: 0;
        left: -55%;
        transform: rotate(-45deg);
      }
      &:after {
        bottom: -50%;
        right: -65%;
        transform: rotate(-45deg);
      }
      > * {
        z-index: 1;
        position: relative;
      }
    }
  }
  &__price {
    font-size: 3rem;
    font-weight: 800;
    margin-bottom: 11px;
    line-height: 1;
    color: $white;
    small {
      font-size: 1.875rem;
    }
  }
  &__description {
    font-size: 1.875rem;
    color: $white;
    margin-bottom: 25px;
    line-height: 1.1;
  }
}
