@import '../../stylesheets/colors';
.main-card {
  background: $white;
  padding: 55px 30px 32px;
  max-width: 100%;
  margin: 0 15px 30px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 5px;
  box-shadow: -10px 10px 15px rgba(0,0,0,.05);
  &__description {
    color: #262626;
    margin-top: 24px;
  }
  img{
    max-height: 165px;
  }
}
