@import '../../stylesheets/colors';
.partners {
  &__item {
    background: $white;
    padding: 25px;
    border-radius: 5px;
    box-shadow: -10px 10px 15px rgba(0, 0, 0, 0.05);
    max-width: 165px;
    min-height: 100%;
    max-height: 100px;
    display: flex !important;
    align-items: center;
    justify-content: center;
    img {
      max-width: 100%;
      height: auto;
    }
  }
  .slick-list {
    //   padding: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    .slick-slide {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
    }
  }
  .slick-track{
    display: flex;
     align-items: stretch; 
     justify-content: center;
    .slick-slide{
        height: auto;   
        >div{
          height: 100%;
        }    
    }
}
  .slick-arrow {
    background: url("../../assets/arrow.svg") no-repeat center/contain !important;
  }
  .slick-prev{
      transform: rotate(180deg);
  }
  .ant-carousel .slick-prev:hover, .ant-carousel .slick-next:hover, .ant-carousel .slick-prev:focus, .ant-carousel .slick-next:focus{

  }
}
