.our-philosophy {
  background: url("../../assets/map-of-world.png") no-repeat;
  background-size: 842px;
  // background-position: center right;
  background-position: bottom 35% right;
  &__content{
    // margin-top: 175px;
    margin-top: 30px;
  }
  &__screenshots {
    position: relative;
    img {
      position: relative;
      z-index: 1;
    }
    img:nth-child(2) {
      position: absolute;
      z-index: 0;
      top: -55px;
      right: -40px;
    }
    .our-philosophy__plane {
      position: absolute;
      top: -60px;
      right: -225px;
      max-width: 205px;
      height: auto;
    }
  }
  &__text{
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
  }
}
