@import '../../stylesheets/colors';
.main-footer {
  @media screen and (max-width: 996px){
    padding: 0;
    .phone-number{
      display: none;
    }
  }
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 996px){
      justify-content: center;
    }
  }
  &__logo {
    width: 172px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 14px;
    float: left;
    @media screen and (max-width: 996px){
      display: none;
    }
    img {
      max-width: 100%;
      height: auto;
      margin-top: -5px;
    }
  }
  .ant-btn {
    margin-left: 15px;
    margin-right: 15px;
    color: white;
    font-size: 1rem;
    line-height: 1;
  }
  &__auth {
    .ant-btn:last-child {
      margin-right: 0;
      padding-right: 0;
    }
  }
  .ant-menu-dark.ant-menu-horizontal > .ant-menu-item, .ant-menu-dark.ant-menu-horizontal > .ant-menu-submenu{
    font-size: 1rem;
    color: $white;
  }
}
.ant-layout-footer{
  padding-top: 47px;
  padding-bottom: 47px;
}