@import "./fonts";
@import "./colors";
.container,
.container-fluid,
.container-sm,
.container-md,
.container-lg,
.container-xl,
.container-xxl {
  width: 100%;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 576px) {
  .container,
  .container-sm {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container,
  .container-sm,
  .container-md {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container,
  .container-sm,
  .container-md,
  .container-lg {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container,
  .container-sm,
  .container-md,
  .container-lg,
  .container-xl {
    max-width: 1140px;
  }
}

.img-fluid {
  max-width: 100%;
  height: auto;
}
.d-flex {
  display: flex !important;
}
.ant-layout {
  background: $lightgray;
}

.ant-btn-lg {
  font-size: 18px;
  height: 45px;
  border-radius: 4px;
  background: $btn;
  font-weight: 600;
  border: none;
  &:hover {
    background: lighten($btn, 10%);
  }
}

.text-center {
  text-align: center;
}

.check-list {
  li {
    list-style: none;
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    font-size: 1.25rem;
  }
  li:before {
    content: "";
    display: inline-block;
    position: absolute;
    height: 25px;
    width: 25px;
    margin-right: 20px;
    left: -35px;
    top: 2px;
    background-image: url("../assets/check.png");
  }
}

.download-btn {
  display: inline-flex;
  width: 155px;
  height: 55px;
  background-color: $black;
  border-radius: 5px;
  margin-right: 40px;
  padding: 5px 10px;
  margin-top: 10px;
  position: relative;
  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;

    top: 0;
    left: 0;

    z-index: 1;
  }
  &--apple {
    &::after {
      background: url("../assets/apple-btn.png") no-repeat center/contain;
      background-size: 135px;
    }
  }
  &--gplay {
    &::after {
      background: url("../assets/google-btn.png") no-repeat center/contain;
      background-size: 130px;
    }
  }
}
.phone-number {
  font-size: 1.125rem;
  line-height: 1;
}
input.ant-input {
  height: 100%;
  font-size: 1.125rem;
  border-radius: 5px;
  border: none;
  color: $black !important;
  padding-right: 20px;
  padding-left: 20px;
  height: 45px;
  margin-bottom: 15px;
  &::placeholder {
    color: #333;
  }
}
.ant-input:focus,
.ant-input-focused {
  color: #333;
}

.pr-0 {
  padding-right: 0 !important;
}
.title {
  margin-bottom: 90px !important;
}
.belka-section {
  padding-top: 110px;
  padding-bottom: 110px;
}
.background--primary {
  background-color: $primary !important;
  * {
    color: $white !important;
  }
}
.background--white {
  background-color: $white !important;
  * {
    color: $black !important;
  }
}
section {
  overflow-x: hidden;
}
@media screen and (max-width: 576px) {
  html,
  body {
    font-size: 12px;
  }
}
.mb-3 {
  margin-bottom: 2rem;
}
@media screen and (max-width: 576px) {
  .items-mobile-center {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
}
