@import '../../stylesheets/colors';
@media screen and (max-width: 996px) {
  .ant-layout-header {
    padding-left: 10px;
    padding-right: 10px;
  }
}
.main-header {
  display: flex;
  align-items: center;
  &__content {
    @media screen and (min-width: 996px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &--desktop {
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media screen and (max-width: 996px) {
        display: none;
      }
    }
  }
  &__logo {
    width: 172px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 14px;
    float: left;
    img {
      max-width: 100%;
      height: auto;
      margin-top: -5px;
    }
  }
  .ant-btn {
    margin-left: 15px;
    margin-right: 15px;
    color: white;
    font-size: 1rem;
    line-height: 1;
  }
  &__auth {
    .ant-btn:last-child {
      margin-right: 0;
      padding-right: 0;
    }
  }
  .ant-menu-dark.ant-menu-horizontal > .ant-menu-item,
  .ant-menu-dark.ant-menu-horizontal > .ant-menu-submenu {
    font-size: 1rem;
    color: $white;
  }
}
@import "~antd/dist/antd.css";

.barsMenu {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none;
  background: none;
  border: none;
  // display: flex;
  // align-items: center;
  margin-top: 0;
  // justify-content: center;
}
.barsBtn {
  display: block;
  width: 20px;
  height: 2px;
  background: $white;
  position: relative;
}
.barsBtn:after,
.barsBtn:before {
  content: attr(x);
  width: 20px;
  position: absolute;
  top: -6px;
  left: 0;
  height: 2px;
  background: $white;
}
.barsBtn:after {
  top: auto;
  bottom: -6px;
}
.ant-drawer-body {
  // padding: 0;
}
.ant-drawer-body .ant-menu-horizontal > .ant-menu-item,
.ant-drawer-body .ant-menu-horizontal > .ant-menu-submenu {
  display: inline-block;
  width: 100%;
}
.ant-drawer-body .ant-menu-horizontal {
  border-bottom: none;
}
.ant-drawer-body .ant-menu-horizontal > .ant-menu-item:hover {
  border-bottom-color: transparent;
}
.ant-drawer-body .main-header__auth{
  display: flex;
  flex-direction: column;
}
.ant-drawer-body .main-header__auth .ant-btn{
  margin-bottom: 15px;
  margin-left: 5px;
  margin-right: 5px;
}
.ant-drawer-body .ant-menu-item{
  text-align: center;
}
.ant-drawer-body .main-header__auth .ant-btn-background-ghost{
  color: #000;
  border-color: #000;
}
@media (max-width: 996px) {
  .barsMenu {
    display: inline-block;
  }
  .ant-menu-dark .ant-menu-item, .ant-menu-dark .ant-menu-item-group-title, .ant-menu-dark .ant-menu-item > a, .ant-menu-dark .ant-menu-item > span > a{
    color: #000;
    &:hover{
      color: #000;
    }
  }
  
}
